import React, { useState, useRef, useEffect } from "react";
import SendIcon from '@mui/icons-material/Send';
import fileAttachIcon from '../assets/img/fileAttach.png';
import fileIcon from '../assets/img/file.png';
import CloseIcon from '@mui/icons-material/Close';

const WidgetKeypad = (props) => {
  //State
  const [inputField, setInputField] = useState("");
  const [uploadWidth, setUploadWidth] = useState("");
  const [file, setFile] = useState({
    selected: false,
    base64: "",
    fileName: "",
  });
  var tempURObj = {
    reply: false,
    replyIndex: 0,
    replyType: "",
    msg: "",
    base64: "",
    fileName: ""
  }
  var tempBRObj = {
    msgType: "",
    msg: "",
    flexMsgs:[]
  }

  //Ref
  const inputFileRef = useRef(null);
  const inputTextRef = useRef(null);
  const fileUploadedRef = useRef(null);

  useEffect(() => {
    setUploadWidth(fileUploadedRef.current.clientWidth);
    inputTextRef.current.focus()
  }, [file.selected])

  useEffect(() => {
    SendMessage(props.flexMsg)
    props.setFlexMsg("")
  }, [props.flexMsg])

  //styles
  const uploadedFileStyle = {
    position: 'absolute',
    marginLeft: '2%',
    zIndex: '20',
    display: file.selected ? 'inline-block' : 'none',
    backgroundColor: '#6072E6',
    top: '8px',
    padding: '6px 10px 2px 6px',
    maxWidth: '40%',
    color: 'white',
    borderRadius: '20px',
    height:"25px"
  }

  const CloseBtnStyles = {
    border: 'none',
    float: 'right',
    backgroundColor: 'red',
    color: 'white',
    borderRadius: '20px',
    marginTop: '-20px',
    padding: '1px',
  }

  const chatbotelementKeypadInput = {
    background: '#f1f0f0',
    padding: '0.5em',
    width: '86%',
    marginLeft: '1%',
    borderRadius: '20px',
    border: '0',
    paddingLeft: file.selected ? uploadWidth + 10 : '40px',
    height: '40px',
    boxSizing: 'border-box',
    marginTop: '5px',
    position: 'absolute',
  }

  const chatbotelementKeypadBtn = {
    position: 'absolute',
    backgroundColor: 'transparent',
    zIndex: '11',
    border: 'none',
    color: '#6072e6',
    fontSize: '40px',
    marginLeft: '5px',
    marginTop: '10px'
  }

  const chatbotelementKeypad = {
    backgroundColor: 'gainsboro',
    height: '50px',
    position: 'absolute',
    zIndex:'900',
    float: 'right',
    bottom: '0px',
    width: '320px'
  }

  const fileInlineStyle = {
    marginLeft: '5px',
    display: 'inline'
  }

  const keypadFileStyle = {
    display: 'inline-block',
    maxHeight: 28,
    maxWidth: '56%',
    marginLeft: '5px',
    textOverflow: 'ellipsis',
    overflow: 'hidden',
    whiteSpace: 'nowrap'
  }

  const sendButton = {
    color: "#6072E6",
    marginLeft: "90%",
    zIndex: "11",
    marginTop: '13px'
  }

  //fuction
  const UploadReset = () => {
    let clonedFile = JSON.parse(JSON.stringify(file));
    clonedFile.selected = false;
    clonedFile.base64 = "";
    clonedFile.fileName = "";
    setFile(clonedFile);
  }

  const handleKeypress = e => {
    if (e.keyCode == 13) {
      SendMessage(e.target.value)
    }
  };

  const SendMessage = (input) => {
    // console.log("pressed")
    if (input != "" || file.selected) {
      PushUserInput(input)
    }
  }

  const PushUserInput = (input) => {
    let clonedURObj = JSON.parse(JSON.stringify(tempURObj));
    if (file.selected) {
      clonedURObj.base64 = file.base64;
      clonedURObj.fileName = file.fileName;
    }
    clonedURObj.msg = input;
    if (props.replyMsg.display) {
      clonedURObj.reply = true;
      clonedURObj.replyIndex = props.replyMsg.index;
      clonedURObj.replyType = props.replyMsg.msgType;
      let clonedReplyMsg = JSON.parse(JSON.stringify(props.replyMsg));
      clonedReplyMsg.display = false;
      props.setReplyMsg(clonedReplyMsg);
    }
    props.setUserInputList([...props.userinputList, clonedURObj]);
    console.log(clonedURObj);
    setInputField("");
    DecideBotResponse(input);
  }

  const DecideBotResponse = (userInput) => {
    let clonedBRObj = JSON.parse(JSON.stringify(tempBRObj));
    if (userInput == "hi" || file.base64.includes(":image/") || file.base64.includes("data:")) {
      clonedBRObj.msgType = "normal";
      if (userInput == "hi") {
        clonedBRObj.msg = "hi";
      } else if (file.base64.includes(":image/")) {
        clonedBRObj.msg = "picture";
      } else if (file.base64.includes("data:")) {
        clonedBRObj.msg = "file";
      }
    } else if (userInput == "button") {
      clonedBRObj.msgType = "button";
      clonedBRObj.msg = "is it correct?";
    } else if (userInput == "list") {
      clonedBRObj.msgType = "list";
      clonedBRObj.msg = "multible option:";
      clonedBRObj.flexMsgs = ["hi","how can I help you", "successful","last element","please give me more information"]
    } else {
      clonedBRObj.msgType = "normal";
      clonedBRObj.msg = "I couldn't get that. Let' try something else!";
    }
    UploadReset()
    props.setBotResponseList([...props.botResponseList, clonedBRObj]);
  }


  const uploadFile = e => {
    // props.setUserInputList([...props.userinputList, e.target.value]);
    const file = e.target.files[0];
    const fileName = e.target.files[0].name;
    e.target.value = null;
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (event) => {
        resolve(event.target.result);
      };

      reader.onerror = (err) => {
        reject(err);
      };
      reader.readAsDataURL(file);
      reader.onload = function () {
        //console.log(reader.result);
        let clonedFile = JSON.parse(JSON.stringify(file));
        clonedFile.selected = true;
        clonedFile.base64 = reader.result;
        clonedFile.fileName = fileName;
        setFile(clonedFile);
        //PushUserInput(reader.result,fileName)
      }
    });
  }

  return (
    <div style={chatbotelementKeypad}>
      <span>
        <button style={chatbotelementKeypadBtn} onClick={() => inputFileRef.current.click()}>
          <img src={fileAttachIcon} style={{ maxWidth: '25px', maxHeight: '25px' }} />
          <input style={{ display: 'none' }} type="file" ref={inputFileRef}  onChange={(e) => uploadFile(e)} />
        </button>
      </span>
      <div style={uploadedFileStyle} ref={fileUploadedRef}>
        <div style={fileInlineStyle}>
          <img src={fileIcon} style={{ maxWidth: '20px', maxHeight: '20px' }} />
        </div>
        <div style={keypadFileStyle}>
          {file.fileName}
        </div>
        <div style={fileInlineStyle}>
          <button style={CloseBtnStyles} onClick={() => UploadReset()}>
            <CloseIcon style={{maxWidth: '20px', maxHeight: '20px'} } />
          </button>
        </div>
      </div>
      <input
        ref={inputTextRef}
        onKeyDown={handleKeypress}
        style={chatbotelementKeypadInput}
        type="text"
        placeholder="Type a message..."
        value={inputField}
        onChange={(e) => setInputField(e.target.value)}
        autoComplete="off"
        autoFocus
      />
      <SendIcon style={sendButton} onClick={() => SendMessage(inputTextRef.current.value)} />
    </div>
  );
};

export default WidgetKeypad;
