import React, { Component } from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Button from '@mui/material/Button';
import listMenuIcon from '../../assets/img/listMenuBtn.png';

import BotWrapper from './BotWrapper';
const BotListResponse = (props) => {
    const accordionStyle = {
        // width: '177px',
        marginTop: '-7px',
        border: 'none',
        overflow: 'hidden',
        backgroundColor: 'white',
        borderRadius: '0px 0px 10px 10px',
        boxShadow: 'none'
    }

    const listBubbleStyle = {
        margin: "0px -13px -8px -13px",

    }

    const listMenuBtnStyle = {
        height: '5px',
        marginLeft: '50px',
        marginBottom: '-10px'
    }

    const listTitleStyle = {
        fontSize: '15px',
        marginTop: '10px',
        height: '20px',
        width: '140px',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
        whiteSpace: 'nowrap',
        color: '#2078d5'
    }

    const listItemStyle = {
        width: '186px',
        textTransform: 'none',
        padding: '5px 0px 5px 0px',
        marginLeft:'-15px'
    }

    const flexMsgList = props.botResponseList[props.index].flexMsgs
    return (
        <div style={listBubbleStyle}>
            <p style={{ margin: '0px 0px 0px 10px' }}>{props.msg}</p>
            <hr />
            <Accordion style={accordionStyle} square={true}>
                <AccordionSummary style={listMenuBtnStyle}>
                    <img src={listMenuIcon} style={{ maxWidth: '35px', maxHeight: '35px' }} />
                    <p style={listTitleStyle}>Main Menu</p>
                </AccordionSummary>
                <AccordionDetails>
                    {flexMsgList.map((msg, index) => {
                        return (
                            <Button style={listItemStyle} variant="outlined" onClick={() => props.clickFunction(msg)}>{msg}</Button>
                        );
                    })}
                    
                    {/* <Button style={{ width: '100%' }} variant="outlined">btn2</Button>
                    <Button style={{ width: '100%' }} variant="outlined">btn3</Button>
                    <Button style={{ width: '100%' }} variant="outlined">btn4</Button> */}
                </AccordionDetails>
            </Accordion>
        </div>
    )
}
export default BotWrapper(BotListResponse);