import React from 'react';
const GetReplyMsg = (props) => {
    //style
    const ReplyMsgStyles = {
        backgroundColor: '#e5e7e9',
        color: '#8998a9',
        margin: '5px',
        borderRadius: '10px',
        paddingLeft: '5px',
        maxWidth: '300px',
        height: 30,
        textOverflow: 'ellipsis',
        overflow: 'hidden',
        whiteSpace: 'nowrap'
    }

    const CloseBtnStyles = {
        border: 'none',
        float: 'right',
        margin: '5px 5px 0px 0px',
        backgroundColor: 'grey  ',
        color: 'white',
        borderRadius: '10px',
        paddingTop: '2px'
    }

    //function
    const ReplyMsgContent = (prop) => {
        return (
            <div style={ReplyMsgStyles}>
                {prop.msg}
            </div>
        );
    }

    const CloseReplyMsg = () => {
        let clonedReplyMsg = JSON.parse(JSON.stringify(props.replyMsg));
        clonedReplyMsg.display = false;
        clonedReplyMsg.msgType =  "";
        props.setReplyMsg(clonedReplyMsg);
    }

    if (props.replyMsg.msgType == "User") {
        let msg = "";
        if (props.userinputList[props.replyMsg.index].fileName != "") {
            msg = props.userinputList[props.replyMsg.index].fileName
        } else {
            msg = props.userinputList[props.replyMsg.index].msg
        }
        return (
            <div >
                User
                <button style={CloseBtnStyles} onClick={() => CloseReplyMsg()}>X</button>
                <ReplyMsgContent msg={msg} />
            </div>
        )
    } else if (props.replyMsg.msgType == "Bot") {
        return (
            <div >
                Bot
                <button style={CloseBtnStyles} onClick={() => CloseReplyMsg()}>X</button>
                <ReplyMsgContent msg={props.botResponseList[props.replyMsg.index].msg} />
            </div>
        )
    }
}
export default GetReplyMsg;