import React, { useState, useEffect, useRef } from "react";
import WidgetChat from "./WidgetChat";
import WidgetKeypad from "./WidgetKeypad";
import WidgetHeader from "./WidgetHeader";
import GetReplyMsg from './UserInput/GetReplyMsg';

const Widget = (props) => {
  //State
  const [replyReview, setReplyReview] = useState("");
  const [flexMsg, setFlexMsg] = useState("");
  const [replyMsg, setReplyMsg] = useState({
    display: false,
    msgType: ""
  });
  
  const charBottomRef = useRef();
  const divRef = useRef([]);
  divRef.current = [];
  useEffect(() => { showReplyMsg(); }, [replyMsg]);

  //styles
  const chatbotelementWidget = {
    zIndex: 9999,
    display: props.showWidget ? 'inline-block' : 'none',
    width: '320px',
    right: '15px',
    height: '450px',
    bottom: '5%',
    position: 'fixed',
    background: '#f6f6f6',
    borderRadius: '10px 10px 10px 10px',
    boxShadow: '0px 2px 10px 1px #10040459'
  }

  const replyMsgReview = {
    display: replyMsg.display ? 'inline-block' : 'none',
    position: 'absolute',
    bottom: '50px',
    backgroundColor: '#dcdcdc',
    width: '100%',
    height: 60
  }

  //function
  const showReplyMsg = () => {
    if (replyMsg.display) {
      setReplyReview(
        <GetReplyMsg
          userinputList={props.userinputList}
          botResponseList={props.botResponseList}
          setReplyMsg={setReplyMsg}
          replyMsg={replyMsg}
        />
      );
    }
  }

  const addToDivRef = (el) => {
    if (el && !divRef.current.includes(el)) {
      divRef.current.push(el);
    }
  }

  return (
    <div style={chatbotelementWidget}>
      {/* Widget Header */}
      <WidgetHeader
        setShowWidget={props.setShowWidget} />
      {/* Widget Chat Section */}
      <WidgetChat
        {...props}
        replyMsg={replyMsg}
        setReplyMsg={setReplyMsg}
        addToDivRef={addToDivRef}
        charBottomRef={charBottomRef}
        divRefCurrent={divRef.current}
        divRef={divRef}
        flexMsg={flexMsg}
        setFlexMsg={setFlexMsg}
      />
      <div style={replyMsgReview}>{replyReview}</div>
      {/* Widget Keypad Section */}
      <WidgetKeypad
        userinputList={props.userinputList}
        setUserInputList={props.setUserInputList}
        botResponseList={props.botResponseList}
        setBotResponseList={props.setBotResponseList}
        replyMsg={replyMsg}
        setReplyMsg={setReplyMsg}
        flexMsg={flexMsg}
        setFlexMsg={setFlexMsg}
      />
    </div>
  );
};

export default Widget;
