import React from 'react';
import ContextMenu from "../BotResponse/ContextMenu";
import ReplyIcon from '@mui/icons-material/Reply';

const UserWrapper = (WrappedComponent) => {
    class Wrapper extends React.Component {
        constructor(props) {
            super(props)
            this.state = {
                hover: false
                //     rightClick: false,
                //     positionY: 0,
                //     positionX: 0
            };
        }

        render() {
            //style
            const replyAreaStyles = {
                backgroundColor: '#e5e7e9',
                border: 'none',
                padding: '5px 15px 0px 10px',
                borderRadius: '5px',
                width: '100%',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                whiteSpace: 'nowrap',
                float: 'left'
            }

            const replyMsgStyles = {
                height: 20,
                width: '100%',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                whiteSpace: 'nowrap',
                textAlign: 'left',
                float: 'left',
                color: "#8998a9"
            }

            const ChatbotelementUserAvatar = {
                animation: 'animateElement linear 0.3s',
                animationIterationCount: 1,
                borderRadius: '50%',
                width: '1.5em',
                height: '1.5em',
                fontSize: '16px',
                float: 'right',
                marginRight: '5px',
                border: '2px solid #6072e6'
            }
            const chatbotelementUserMsg = {
                animation: 'animateElement linear 0.2s',
                animationIterationCount: 1,
                marginTop: '5px',
                wordWrap: 'break-word',
                padding: '5px 10px 5px 10px',
                float: 'right',
                marginRight: '0.5em',
                background: '#6072e6',
                border: '1px solid #6072e6',
                color: '#fff',
                borderRadius: '1em',
                marginBottom: '0.15em',
                fontSize: '12px', 
                minWidth: '5%',
                maxWidth: '55%',
                lineHeight: '1.5em'
            }

            const userContextMenuBtn = {
                backgroundColor: 'transparent',
                border: 'none',
                padding: 'none',
                color: 'white'
            }

            const userReplyStyle = {
                visibility: this.state.hover ? 'visible' : 'hidden',
                backgroundColor: '#DCDCDC',
                color: 'white',
                borderRadius: '20px',
                padding: '2px',
                margin: '8px 8px 0px 0px',
                transform: 'scaleX(-1)',
                float: 'right'
            }

            //function
            const RightClick = (e) => {
                // e.preventDefault();
                // // this.setState({
                // //     rightClick: true,
                // //     positionY: e.clientY,
                // //     positionX: e.clientX,
                // // })
                // //console.log(this.props.divRefCurrent[this.props.index].clientHeight);
                // let clonedReplyMsg = JSON.parse(JSON.stringify(this.props.replyMsg));
                // clonedReplyMsg.display = true;
                // clonedReplyMsg.msgType = "User";
                // clonedReplyMsg.index = this.props.index;
                // this.props.setReplyMsg(clonedReplyMsg);
            }

            const ScrollToMessage = () => {
                let index = this.props.userinputList[this.props.index].replyIndex;
                if (this.props.userinputList[this.props.index].replyType == "Bot") {
                    index = index + index + 1
                } else if (this.props.userinputList[this.props.index].replyType == "User") {
                    index = index * 2
                }
                const replyRef = this.props.divRefCurrent[index]
                replyRef.scrollIntoView()
                replyRef.style.backgroundColor = "#6dfc18";
                replyRef.style.transition = '0.001s';
            }

            const ReplyMsgContent = () => {
                if (this.props.userinputList[this.props.index].replyType == "Bot") {
                    return (
                        this.props.botResponseList[this.props.userinputList[this.props.index].replyIndex].msg
                    );
                } else if (this.props.userinputList[this.props.index].replyType == "User") {
                    if (this.props.userinputList[this.props.userinputList[this.props.index].replyIndex].fileName != "") {
                        return (
                            this.props.userinputList[this.props.userinputList[this.props.index].replyIndex].fileName
                        );
                    } else {
                        return (
                            this.props.userinputList[this.props.userinputList[this.props.index].replyIndex].msg
                        );
                    }
                }
            }

            const ShowReplyMsg = () => {
                if (this.props.userinputList[this.props.index].reply) {
                    return (
                        <div>
                            <button style={replyAreaStyles} onClick={() => ScrollToMessage()} >
                                <div style={replyMsgStyles}>
                                    {ReplyMsgContent()}
                                </div>
                            </button>
                        </div>
                    );
                }
            }

            const replyMessage = () => {
                let clonedReplyMsg = JSON.parse(JSON.stringify(this.props.replyMsg));
                clonedReplyMsg.display = true;
                clonedReplyMsg.msgType = "User";
                clonedReplyMsg.index = this.props.index;
                this.props.setReplyMsg(clonedReplyMsg);
            }

            const HandleTransitionEnd = (index) => {
                const curRef = this.props.divRefCurrent[(index * 2)];
                curRef.style.backgroundColor = "#f6f6f6";
                curRef.style.transition = '0.2s';
            }

            const DivOnHover = () => {
                this.setState({
                    hover: true
                })
            }

            const DivOnLeave = () => {
                this.setState({
                    hover: false
                })
            }

            // const ShowContextMenu = () => {
            //     return (
            //         <ContextMenu
            //             {...this.props}
            //             display={this.state.rightClick}
            //             positionY={this.state.positionY}
            //             positionX={this.state.positionX}
            //             msgType="User"
            //         ></ContextMenu>
            //     );
            // }
            return (
                <div
                    ref={this.props.addToDivRef}
                    onMouseEnter={() => DivOnHover()}
                    onMouseLeave={() => DivOnLeave()}
                    onTransitionEnd={() => HandleTransitionEnd(this.props.index)}
                >
                    {/* <img style={ChatbotelementUserAvatar} src="./assets/img/userAvatar.jpg" /> */}
                    <div style={chatbotelementUserMsg}>
                        <ShowReplyMsg />
                        {/* <button style={userContextMenuBtn} onContextMenu={(e) => RightClick(e)}> */}
                            <WrappedComponent {...this.props} />
                        {/* </button> */}
                    </div>
                    <ReplyIcon style={userReplyStyle} onClick={() => replyMessage()} />
                    {/* <div><ShowContextMenu /></div> */}
                    <div style={this.props.clearFix} />
                </div >
            )
        }
    }
    return Wrapper;
}
export default UserWrapper;