import React, { Component, useEffect } from 'react';
import ContextMenu from "./ContextMenu";
import ReplyIcon from '@mui/icons-material/Reply';
const BotWrapper = (WrappedComponent) => {
    class Wrapper extends React.Component {
        constructor(props) {
            super(props)
            this.state = {
                hover: false
                //     rightClick: false,
                //     positionY: 0,
                //     positionX: 0
            };
        }

        render() {
            let test = false;
            //style
            const chatbotelementBotAvatar = {
                borderRadius: '50%',
                width: '1.5em',
                height: '1.5em',
                fontSize: '16px',
                float: 'left',
                marginLeft: '5px',
                border: '2px solid #6072e6',
            }

            const chatbotelementBotMsg = {
                color: '#000000',
                float: 'left',
                marginTop: '5px',
                background: '#ffffff',
                boxShadow: '2px 5px 5px 1px #efeef5',
                border: '1px solid #ffffff',
                marginLeft: '0.5em',
                padding: '0.6em 1em',
                borderRadius: '1em',
                maxWidth: '55%',
                minWidth: '10%',
                lineHeight: '1.5em',
                fontSize: '12px',
                wordWrap: 'break-word',
            }

            const botContextMenuBtn = {
                backgroundColor: 'transparent',
                border: 'none',
                padding: 'none'
            }

            const botReplyStyle = {
                visibility: this.state.hover ? 'visible' : 'hidden',
                backgroundColor: '#DCDCDC',
                color: 'white',
                borderRadius: '20px',
                padding: '2px',
                margin: '10px 0px 0px 8px',
            }

            //function
            const RightClick = (e) => {
                // e.preventDefault();
                // // this.setState({
                // //     rightClick: true,
                // //     positionY: e.clientY,   
                // //     positionX: e.clientX,
                // // })
                // // console.log(window.innerWidth);
                // // console.log(e.clientY);
                // // console.log(e.clientX);
                // let clonedReplyMsg = JSON.parse(JSON.stringify(this.props.replyMsg));
                // clonedReplyMsg.display = true;
                // clonedReplyMsg.msgType = "Bot";
                // clonedReplyMsg.index = this.props.index;
                // this.props.setReplyMsg(clonedReplyMsg);
            }

            const replyMessage = () => {
                let clonedReplyMsg = JSON.parse(JSON.stringify(this.props.replyMsg));
                clonedReplyMsg.display = true;
                clonedReplyMsg.msgType = "Bot";
                clonedReplyMsg.index = this.props.index;
                this.props.setReplyMsg(clonedReplyMsg);
            }

            // const ShowContextMenu = () => {
            //     return (
            //         <ContextMenu
            //             {...this.props}
            //             display={this.state.rightClick}
            //             positionY={this.state.positionY}
            //             positionX={this.state.positionX}
            //             msgType="Bot"
            //         ></ContextMenu>
            //     );
            // }

            const HandleTransitionEnd = (index) => {
                const curRef = this.props.divRefCurrent[(index + index) + 1];
                curRef.style.backgroundColor = "#f6f6f6";
                curRef.style.transition = '0.2s';
            }

            const DivOnHover = () => {
                this.setState({
                   hover: true
                })
            }

            const DivOnLeave = () => {
                this.setState({
                    hover: false
                 })
            }


            return (
                <div
                    ref={this.props.addToDivRef}
                    onMouseEnter={() => DivOnHover()}
                    onMouseLeave={() => DivOnLeave()}
                    onTransitionEnd={() => HandleTransitionEnd(this.props.index)}
                >
                    {/* <img style={chatbotelementBotAvatar} src="./assets/img/botAvatar.png" /> */}
                    <div style={chatbotelementBotMsg}>
                        {/* <div style={botContextMenuBtn} onContextMenu={(e) => RightClick(e)}> */}
                        <WrappedComponent {...this.props} />
                        {/* </div> */}
                        {/* <div><ShowContextMenu /></div> */}
                    </div>
                    <ReplyIcon style={botReplyStyle} onClick={() => replyMessage()} />
                    <div style={this.props.clearFix} />
                </div >
            )
        }
    }
    return Wrapper;
}
export default BotWrapper;