import React from "react";
import CloseIcon from '@mui/icons-material/Close';

const WidgetHeader = (props) => {
  //styles
  const chatbotelementChatHeader = {
    width: '100%',
    height: '10%',
    lineHeight: '32px',
    background: '#6072e6',
    borderRadius: '10px 10px 0px 0px',
    padding: '5px',
    fontSize: '20px',
    boxSizing: 'border-box'
  }

  const chatbotelementChatHeaderName = {
    color: 'white',
    marginLeft: '5px'
  }

  const chatbotelementChatHeaderCloseBtn = {
    color: "white",
    marginRight: 5,
    float: "right",
    marginTop: 5,
    cursor: 'pointer'
  }

  const chatbotelementMaterialIcons = {
    fontFamily: 'Material Icons',
    fontWeight: 'normal',
    fontStyle: 'normal',
    fontSize: '24px',
    lineHeight: '1',
    letterSpacing: 'normal',
    textTransform: 'none',
    display: 'inline-block',
    whiteSpace: 'nowrap',
    wordWrap: 'normal',
    direction: 'ltr',
    webkitFontFeatureSettings: 'liga',
    webkitFontSmoothing: 'antialiased'
  }

  return (
    <div style={chatbotelementChatHeader}>
      {/* Header Name */}
      <span style={chatbotelementChatHeaderName}>
        ChatBot
      </span>
      {/* Header Close Icon */}
      <span style={chatbotelementChatHeaderCloseBtn} onClick={() => props.setShowWidget(false)}>
        <CloseIcon></CloseIcon>
      </span>
      
    </div>
  );
};

export default WidgetHeader;
