import React, { useState, useRef } from "react";
import BotAvatar from "../assets/img/botAvatarWhite.png"; 
import Widget from "./Widget";

const Profile = () => {
  //State
  const [showWidget, setShowWidget] = useState(false);
  const [userinputList, setUserInputList] = useState([]);
  const [botResponseList, setBotResponseList] = useState([]);

  //Styles
  const chatbotelementProfileDiv = {
    zIndex: 9999,
    position: 'fixed',
    padding: '5px',
    width: '60px',
    bottom: '1%',
    right: '2%',
    cursor: 'pointer'
  }

  const chatbotelementImgProfile = {
    boxShadow: '0 0px 1px 0 rgba(0, 0, 0, 0.16), 0 0px 10px 0 rgba(0, 0, 0, 0.12)',
    background: '#6072E6',
    borderRadius: '50px',
    width: '100%',
    height:'60px',
    padding: '10px'
  }

  if (showWidget) {
    return (
      <Widget
        userinputList={userinputList}
        setUserInputList={setUserInputList}
        botResponseList={botResponseList}
        setBotResponseList={setBotResponseList}
        showWidget={showWidget}
        setShowWidget={setShowWidget}
      />
    );
  } else {
    return (
      <div style={chatbotelementProfileDiv}>
        <img
          style={chatbotelementImgProfile}
          onClick={() => setShowWidget(true)}
          src={BotAvatar}
        />
      </div>
    );
  }

};

export default Profile;
