import Button from '@mui/material/Button';
import React, { Component } from 'react';
import BotWrapper from './BotWrapper';
const BotButtonResponse = (props) => {
    const confirm = "Confirm"
    const cancel = "Cancel"

    const botBtnStyle = {
        width: '89px',
        margin: '5px',
        textTransform: 'none',
        borderRadius:'1em',
        backgroundColor:'white'
    }

    console.log(props)
    return (
        //onClick={() => UploadReset()}
        <div>
            <Button variant="text" style={botBtnStyle} onClick={() => props.clickFunction(confirm)}>{confirm}</Button>
            <Button variant="text" style={botBtnStyle} onClick={() => props.clickFunction(cancel)}>{cancel}</Button>
        </div>
    )
}
export default BotButtonResponse;