import React, { useEffect } from "react";
import BotResponse from './BotResponse/BotResponse';
import UserInput from './UserInput/UserInput';

const WidgetChat = (props) => {
  //Effect
  useEffect(() => {
    props.charBottomRef.current?.scrollIntoView();
  }, [props.divRefCurrent]);

  //Styles
  const chatbotelementChats = {
    height: '340px',
    padding: '5px',
    marginTop: '5px',
    borderRadius: '1px',
    overflowY: 'scroll',
    overflowX: 'hidden',
    position: 'relative',
    zIndex: '9999',
    transition: '0.2s',
    maxHeight: props.replyMsg.display ? "280px" : "340px"
  }

  const chatbotelementClearfix = {
    marginTop: '2px',
    marginBottom: '2px'
  }

  const chatbotelementClearfixStyle = {
    clear: 'both'
  }

  //create dialogue 
  const dialogue = [];
  if (props.showWidget) {
    for (let i = 0; i < props.userinputList.length; i++) {
      dialogue.push(
        <div key={i}>
          <UserInput {...props} clearFix={chatbotelementClearfixStyle} index={i} />
          <BotResponse  {...props} clearFix={chatbotelementClearfixStyle} index={i} />
        </div>
      )
    }
  }

  return (
    <div style={chatbotelementChats} >
      {dialogue}
      <div ref={props.charBottomRef}  style={chatbotelementClearfix} />
    </div>
  );
};

export default WidgetChat;
