import React from 'react';
import UserWrapper from './UserWrapper';
const UserFileInput = (props) => {
    console.log(props)
    return (
        <div>
            <div style={{border:'solid', padding:'5px'}}>
                <img src="./assets/img/file.png" style={{ maxWidth: '50px', maxHeight: '50px' }} />
                <br/>
                {props.userinputList[props.index].fileName}
            </div>
            <div style={props.responseStyle}>
                {props.userinputList[props.index].msg}
            </div>
        </div>
    )
}
export default UserWrapper(UserFileInput);