import React, {Component} from 'react';
import BotWrapper from './BotWrapper';
const BotNormalResponse = (props) => {
    return (
        <div
            style={{ width: props.botResponseList[props.index].msgType == "button" ? '280px' : '100%' }}
        >
            {props.msg}
        </div>
    )
}
export default BotWrapper(BotNormalResponse);