import React from 'react';
import UserNormalInput from './UserNormalInput';
import UserPictureInput from './UserPictureInput';
import UserFileInput from './UserFileInput';
const UserInput = (props) => {
    //style
    const userResponse = {
        overflow: 'hidden',
        maxWidth: '250px',
        float: 'left',
        textAlign: 'left',
    }

    const base64 = props.userinputList[props.index].base64;
    if (base64.includes(":image/")) {
        return (
            <UserPictureInput {...props} responseStyle={userResponse} src={base64} index={props.index} />
        );
    } else if (base64.includes("data:")) {
        return (
            <UserFileInput {...props} responseStyle={userResponse} index={props.index} />
        );
    }else {
        return (
            <UserNormalInput {...props} responseStyle={userResponse} msg={props.userinputList[props.index].msg} index={props.index}/>
        );
    }
}
export default UserInput;