import React, { useState,useEffect } from 'react';
const ContextMenu = (props) => {
    useEffect(() => {
            console.log("triggered22")
            setDisplay(props.display)
    }, [props.display]);
    const [display, setDisplay] = useState(false);

    const setReply = () => {
        let clonedReplyMsg = JSON.parse(JSON.stringify(props.replyMsg));
        clonedReplyMsg.display = true;
        clonedReplyMsg.msgType =  props.msgType;
        clonedReplyMsg.index = props.index;
        props.setReplyMsg(clonedReplyMsg);
        console.log(props);
    }

    const contextMenuStyles = {
        position: 'absolute',
        //left: (props.positionX-600 ),
        //top: (props.positionY-240 ),
    }

    const contextMenuBtnStyles = {
        width: '60px',
        height: '30px',
        backgroundColor: 'white',
        border: 'ridge'
    }

    if (display) {
        return (
            <div style={contextMenuStyles}>
                <button style={contextMenuBtnStyles} onClick={() =>setReply()}>reply</button>
                <br />
                <button style={contextMenuBtnStyles} onClick={() =>setDisplay(false)}>cancel</button>
            </div>
        )
    } else {
        return null;
    }

}
export default ContextMenu;