import React from 'react';
import UserWrapper from './UserWrapper';
const UserPictureInput = (props) => {
    return (
        <div>
            <div style={{ backgroundColor: 'gainsboro',border:'solid', padding:'5px'}}>
                <img src={props.src} style={{ maxWidth: '130px' }} />
                {/* {props.userinputList[props.index].fileName} */}
            </div>
            <div style={props.responseStyle}>
                {props.userinputList[props.index].msg}
            </div>
        </div>
    )
}
export default UserWrapper(UserPictureInput);