import React from 'react';
import BotNormalResponse from './BotNormalResponse';
import BotButtonResponse from './BotButtonResponse';
import BotListResponse from './BotListResponse';
const BotResponse = (props) => {

  const sendDefalutMsg = (msg) => {
    let clonedReplyMsg = JSON.parse(JSON.stringify(props.replyMsg));
    clonedReplyMsg.display = true;
    clonedReplyMsg.msgType = "Bot";
    clonedReplyMsg.index = props.index;
    props.setReplyMsg(clonedReplyMsg);
    props.setFlexMsg(msg)
  }
  console.log(props)
  if (props.botResponseList[props.index].msgType == "normal") {
    return (
      <BotNormalResponse {...props} msg={props.botResponseList[props.index].msg} ></BotNormalResponse>
    );
  } else if (props.botResponseList[props.index].msgType == "button") {

    return (
      <div>
        <BotNormalResponse {...props} msg={props.botResponseList[props.index].msg} ></BotNormalResponse>
        <BotButtonResponse {...props} msg={props.botResponseList[props.index].msg} clickFunction={sendDefalutMsg}></BotButtonResponse>
      </div>
    );
  } else if (props.botResponseList[props.index].msgType == "list") {
    return (
      <BotListResponse {...props} msg={props.botResponseList[props.index].msg} clickFunction={sendDefalutMsg}></BotListResponse>
    );
  }
}
export default BotResponse;